.project {
  width: 800px;
  padding: 2em;
  margin: 0 auto;
  text-align: center;
  box-shadow: var(--shadow);
  transition: transform 0.2s linear;
}

.project:hover {
  transform: translateY(-7px);
}

.project__description {
  width: 100%;
  margin-top: 2.5em;
  text-align: left;
}

.project__stack {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 1.2em 0;
}

.project__stack-item {
  margin: 0.5em;
  font-weight: 500;
  font-size: 0.8rem;
  color: var(--clr-fg-alt);
}

.project .link--icon {
  margin-left: 0.5em;
}

@media (max-width: 600px) {
  .project {
    align-items: flex-start;
    margin-top: 2em;
    margin-right: 12px;
    left: 0;
    padding: 1em;
    max-width: 378px;
  }
}
